      <transition name="slide" appear mode="out-in">
        <div class="widget--collapse-main">
          <div class="widget--listing-1 w3-padding-48 px-md-5 w3-container">
            <h1 class="my-0"><strong>Add payment method</strong></h1>
          </div>
          <div class="row w3-padding-48">
            <div class="col-12 col-md-8 offset-md-2">
              <div class="container">
                <div class="mt-4 card rounded-0 widget--borderless">
                  <div class="card-body pb-0">
                    <div class="tab-content" id="myTabContent">
                      <div ref="card"></div>
                      <div class="modal-footer mt-4">
                        <button type="button" name="button" class="btn font-weight-medium rounded-pill text-uppercase">Cancel</button>
                        <button type="button" name="button" class="btn font-weight-medium rounded-pill text-uppercase">Sign up</button>
                      </div>
                    </div>
                  </div>
                </div>
                <!--  -->
                <div class="card rounded-0 widget--borderless mt-4">
